import { BrowserRouter as Router, Route } from 'react-router-dom';
import { Container } from 'react-bootstrap';

import Header from './components/Header';
import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import ProductPage from './pages/ProductPage';
import CartPage from './pages/CartPage';
import LoginPage from './pages/LoginPage';
import SignupPage from './pages/SignupPage';
import ProfilePage from './pages/ProfilePage';
import ShippingPage from './pages/ShippingPage';
import PaymentSelectPage from './pages/PaymentSelectPage';
import PlaceOrderPage from './pages/PlaceOrderPage';
import OrderPage from './pages/OrderPage';
import UsersListPage from './pages/UsersListPage';
import UserEditPage from './pages/UserEditPage';
import ProductsListPage from './pages/ProductsListPage';
import ProductEditPage from './pages/ProductEditPage';
import OrdersListPage from './pages/OrdersListPage';
import ProductsTopRatedCarousel from './components/ProductsTopRatedCarousel';

const App = () => {
  return (
    <Router>
      <Header />
      <main className='py-3'>
        {/* {window.location.pathname === '/' && <ProductsTopRatedCarousel />} */}
        <Container>
          {/* {this.state.token && (
                  <Redirect
                    from={'/login'}
                    to={'/'}
                    exact
                  />
                )} */}
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/login`}
            component={LoginPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/signup`}
            component={SignupPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/profile`}
            component={ProfilePage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/products/:id`}
            component={ProductPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/cart/:id?`}
            component={CartPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/shipping`}
            component={ShippingPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/payment`}
            component={PaymentSelectPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/placeorder`}
            component={PlaceOrderPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/order/:id`}
            component={OrderPage}
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/admin/orders-list`}
            component={OrdersListPage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/admin/users-list`}
            component={UsersListPage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/admin/user/:id/edit`}
            component={UserEditPage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/admin/product/:id/edit`}
            component={ProductEditPage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/admin/products-list`}
            component={ProductsListPage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/admin/products-list/page/:pageNumber`}
            component={ProductsListPage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/search/:keyword`}
            component={HomePage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/search/:keyword/page/:pageNumber`}
            component={HomePage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/page/:pageNumber`}
            component={HomePage}
            exact
          />
          <Route
            path={`${process.env.REACT_APP_DEPLOY_URL}/`}
            component={HomePage}
            exact
          />
        </Container>
      </main>
      <Footer />
    </Router>
  );
};

export default App;
