import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

// import MobileMenu from './MobileMenu';
import { Container, Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

import { logout } from '../actions/userActions';
import SearchBox from './SearchBox';

const Header = () => {
  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const history = useHistory();

  const [darkTheme, setDarkTheme] = useState(false);

  let userOptions;
  useEffect(() => {
    // initial state
    userOptions = JSON.parse(localStorage.getItem('userOptions'));
    if (userOptions && userOptions.darkTheme === true) {
      setDarkTheme(true); // opposite
      document.body.classList.add('dark-theme');
    } else if (userOptions && userOptions.darkTheme === false) {
      setDarkTheme(false);
      document.body.classList.remove('dark-theme');
    }
  }, []);

  const darkThemeHandler = (e) => {
    e.preventDefault();
    const userOptions = JSON.parse(localStorage.getItem('userOptions'));
    if (userOptions && userOptions.darkTheme === true) {
      setDarkTheme(false); // opposite
      document.body.classList.remove('dark-theme');
    } else if (userOptions && userOptions.darkTheme === false) {
      setDarkTheme(true);
      document.body.classList.add('dark-theme');
    } else if (!userOptions) {
      setDarkTheme(false);
    }
  };
  useEffect(() => {
    localStorage.setItem(
      'userOptions',
      JSON.stringify({ darkTheme: darkTheme })
    );
  }, [darkTheme]);

  const logoutHandler = () => {
    dispatch(logout());
    history.push('/');
  };

  // The forwardRef is important!!
  // Dropdown needs access to the DOM node in order to position the Menu
  const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
    <span ref={ref} className='nav__user-profile'>
      <i className='fas fa-user'></i>
      {children}
    </span>
  ));

  return (
    <header>
      <Navbar bg='dark' variant='dark' expand='lg' collapseOnSelect>
        <Container>
          <LinkContainer
            to='/'
            title={`${process.env.REACT_APP_SITENAME} E${process.env.REACT_APP_SITETYPE}Shop`}
          >
            <Navbar.Brand>
              <svg
                fill='#ffffff'
                viewBox='0 0 32 32'
                className='svg-icon size-med st000'
                style={{ width: '38px' }}
              >
                <use xlinkHref='#selfie-logo-ls' />
              </svg>
              <span className='text-thin2'>
                ECommerce{/* {process.env.REACT_APP_SITENAME} */}
              </span>
              Shop{/* {process.env.REACT_APP_SITETYPE} */}
            </Navbar.Brand>
          </LinkContainer>

          <Navbar.Toggle aria-controls='basic-navbar-nav' />
          <Navbar.Collapse id='basic-navbar-nav'>
            <SearchBox />
            <Nav className='ms-auto'>
              <Nav.Item as='li'>
                <LinkContainer to='/cart'>
                  <Nav.Link>
                    <i className='fas fa-shopping-basket'></i> Cart
                  </Nav.Link>
                </LinkContainer>
              </Nav.Item>
              <Nav.Item as='li'>
                {userInfo ? (
                  <>
                    <NavDropdown
                      as={CustomToggle}
                      title={`${userInfo.name}`}
                      id='username'
                    >
                      <LinkContainer to='/profile'>
                        <NavDropdown.Item>
                          <i className='fas fa-user-circle'></i>&nbsp;&nbsp;
                          Profile
                        </NavDropdown.Item>
                      </LinkContainer>

                      <NavDropdown.Item onClick={logoutHandler}>
                        <i className='fas fa-door-open'></i>&nbsp;&nbsp; Logout
                      </NavDropdown.Item>
                    </NavDropdown>
                  </>
                ) : (
                  <LinkContainer to='/login'>
                    <Nav.Link>
                      <i className='fas fa-user'></i>&nbsp;&nbsp; Sign In
                    </Nav.Link>
                  </LinkContainer>
                )}
              </Nav.Item>

              {userInfo && userInfo.isAdmin && (
                <Nav.Item as='li'>
                  <NavDropdown
                    as={CustomToggle}
                    title='Administration'
                    id='useradmin'
                  >
                    <LinkContainer to='/admin/users-list'>
                      <NavDropdown.Item>
                        <i className='fas fa-users'></i>&nbsp;&nbsp; Users List
                      </NavDropdown.Item>
                    </LinkContainer>
                    <LinkContainer to='/admin/products-list'>
                      <NavDropdown.Item>
                        <i className='fas fa-grip-lines'></i>&nbsp;&nbsp;
                        Products List
                      </NavDropdown.Item>
                    </LinkContainer>
                    <LinkContainer to='/admin/orders-list'>
                      <NavDropdown.Item>
                        <i className='fas fa-file-alt'></i>&nbsp;&nbsp; Orders
                        List
                      </NavDropdown.Item>
                    </LinkContainer>
                  </NavDropdown>
                </Nav.Item>
              )}
              {/* Last item in menu below */}
              <Nav.Item as='li'>
                <LinkContainer to='/set-theme' onClick={darkThemeHandler}>
                  <Nav.Link>
                    {darkTheme ? (
                      <>
                        <i className='fas fa-cloud-moon'></i> LightMode
                      </>
                    ) : (
                      <>
                        <i className='fas fa-cloud-moon'></i> DarkMode
                      </>
                    )}
                  </Nav.Link>
                </LinkContainer>
              </Nav.Item>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  );
};

export default Header;
