import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Form, Button, Row, Col } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loading from '../components/Loading';
import FormContainer from '../components/FormContainer';
import { loginUser } from '../actions/userActions';
import CheckoutSteps from '../components/CheckoutSteps';

const LoginPage = (props) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);

  const { loading, error, userInfo } = userLogin;

  const history = useHistory();
  const redirect = props ? props.location.search.split('=')[1] : '/';

  useEffect(() => {
    // console.log('userInfo: ', userInfo);
    // console.log('redirect:', redirect);
    // console.log('location.location.search: ', location.location.search);
    if (userInfo && redirect) {
      history.push(redirect);
    } else if (userInfo && !redirect) {
      history.push('/');
    }
  }, [history, userInfo, redirect]);

  const submitHandler = (e) => {
    e.preventDefault();
    // console.log(email, password);
    dispatch(loginUser(email, password));
  };

  return (
    <FormContainer>
      {redirect === 'shipping' ? <CheckoutSteps step1 /> : ''}
      <h1>Sign In</h1>
      {error && <Message variant='danger'>{error}</Message>}
      {loading && <Loading />}
      <Form onSubmit={submitHandler}>
        <Form.Group controlId='email'>
          <Form.Label>Email Address</Form.Label>
          <Form.Control
            type='email'
            placeholder='Enter email'
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            autoComplete='email'
          ></Form.Control>
        </Form.Group>
        <Form.Group controlId='password'>
          <Form.Label>Password</Form.Label>
          <Form.Control
            type='password'
            placeholder='Enter password'
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            autoComplete='password'
          ></Form.Control>
        </Form.Group>
        <Form.Group controlId='buttonLogin'>
          <Button type='submit' variant='primary' className='my-3'>
            Sign In
          </Button>
        </Form.Group>
      </Form>
      <Row className='py-3'>
        <Col>
          New Customer? &nbsp;
          <Link to={redirect ? `/signup?redirect=${redirect}` : '/signup'}>
            Sign Up
          </Link>
        </Col>
      </Row>
    </FormContainer>
  );
};

export default LoginPage;
