import { Container, Row, Col } from 'react-bootstrap';

const Footer = () => {
  return (
    <footer>
      <Container>
        <Row>
          <Col className='text-center py-3'>
            Copyright &copy;
            <span className='text-thin2'>{process.env.REACT_APP_SITENAME}</span>
            {process.env.REACT_APP_SITETYPE}
          </Col>
        </Row>
      </Container>
    </footer>
  );
};

export default Footer;
