import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Row, Col } from 'react-bootstrap';

import Meta from '../components/Meta';
import Message from '../components/Message';
import Loading from '../components/Loading';
import Paginate from '../components/Paginate';
import Product from '../components/Product';
import ProductsTopRatedCarousel from '../components/ProductsTopRatedCarousel';
// import products from '../products';
import { listProducts } from '../actions/productActions.js';

const HomePage = ({ match }) => {
  const keyword = match.params.keyword;
  const pageNumber = match.params.pageNumber || 1;

  // const [products, setProducts] = useState([]);
  const dispatch = useDispatch();

  const productsList = useSelector((state) => state.productsList);

  const { loading, error, products, page, pages } = productsList;

  useEffect(() => {
    dispatch(listProducts(keyword, pageNumber));
    /*
    const fetchProducts = async () => {
      const res = await axios.get('${process.env.REACT_APP_BACKEND_URL}/api/products');

      setProducts(res.data);
    };
    fetchProducts();*/
  }, [dispatch, keyword, pageNumber]);

  return (
    <>
      <Meta
        title={process.env.REACT_APP_SITENAME}
        description='Hand-pick selfie items e-commerce online store'
      />
      {!keyword && <ProductsTopRatedCarousel />}
      <h1 className='product__title-page'>Hand-Picked Products</h1>
      {loading ? (
        <Loading />
      ) : error ? (
        <Message variant='danger'>{error}</Message>
      ) : (
        <>
          <Row>
            {products.map((product) => (
              <Col sm={12} md={6} lg={4} xl={3} key={product._id}>
                <Product product={product} />
              </Col>
            ))}
          </Row>

          {keyword && (
            <Link to='/' className='btn btn-light'>
              Go Back
            </Link>
          )}

          <Paginate
            page={page}
            pages={pages}
            keyword={keyword ? keyword : ''}
          />
        </>
      )}
    </>
  );
};

export default HomePage;
